body {
  font-family: 'Inter', sans-serif;
  background-color: #f5f5f5;
  color: #333;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  padding: 50px 20px;
  background-color: #0098ea;
  color: #fff;
  border-radius: 10px;
  margin-bottom: 40px;
}

header h1 {
  font-size: 2.5rem;
  margin: 0;
}

header p {
  font-size: 1.2rem;
  margin: 10px 0 0;
}

.ton-connect-button {
  background-color: #0077b6;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.ton-connect-button:hover {
  background-color: #005f8d;
}

main {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

section {
  margin-bottom: 40px;
}

section h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

section p {
  font-size: 1rem;
  line-height: 1.5;
}

.domain-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.domain-list li {
  background: #f0f0f0;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(50% - 10px);
}

.domain-list li.sold {
  background: #e0e0e0;
  color: #999;
}

.domain-list li.available {
  background: #0098ea;
  color: #fff;
}

.domain-list li button {
  background: #0077b6;
  color: #fff;
  border: none;
  padding: 5px 10px;
  font-size: 0.9rem;
  border-radius: 5px;
  cursor: pointer;
}

.domain-list li button:hover {
  background: #005f8d;
}
